@import "./_variables.scss";

.contact {
   background: $clr-bg;
   padding-top: 7rem;
   padding-bottom: 7rem;

   &-content {
      padding-top: 2rem;
      display: flex;

      &__intro {
         width: 50%;

         &--text {
            font-size: 1.375rem;
            padding-bottom: 0.8em;
         }

         &--data {

            &__email,
            &__phone {
               display: flex;
               align-items: center;

               span {
                  font-size: 1.1rem;
                  line-height: 2.3em;
                  padding-left: 1em;
               }
            }
         }
      }

      &__form {
         display: flex;
         flex-direction: column;
         align-items: flex-start;
         width: 50%;
         background: rgba(0, 0, 0, 0.05);

         &--user-data {
            display: flex;
            width: 100%;

            input {
               flex-grow: 1;
            }
         }

         &--message {
            width: 100%;
         }
      }
   }
}